import { FirebaseService } from './../service/firebase.service';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';



@Component({
  selector: 'app-detail-page-component',
  templateUrl: './detail-page-component.component.html',
  styleUrls: ['./detail-page-component.component.css']
})
export class DetailPageComponentComponent implements OnInit {

  product$;
  name;
  brand;

  constructor(private activatedRoute: ActivatedRoute, private firebaseService: FirebaseService) { }

  ngOnInit() {
    let id = +this.activatedRoute.snapshot.params['id'];

    this.firebaseService.getSingleProduct(id)
      .subscribe(result => {
        this.product$ = result;
        //console.log(result);
        console.log(this.product$);


      });


    //console.log(this.product);
    // console.log(this.name);
    // console.log("fssdfds");
  }


}
