import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList, AngularFireObject } from 'angularfire2/database';
import { Key } from 'protractor';

const cartProduct = [

];

@Injectable({
  providedIn: 'root'
})


export class FirebaseService {

  constructor(private firebase: AngularFireDatabase) { }
  productList: AngularFireList<any>;
  customerList: AngularFireList<any>;
  isLoggedIn: boolean;
  key;
  confirmPassword: '';
  email: '';
  fullName: '';
  location: '';
  mobile: '';

  setCurrentUserLoginInfo(key, confirmPassword, email, fullName, location, mobile, isLoggedIn) {
    this.key = key;
    this.confirmPassword = confirmPassword;
    this.email = email;
    this.fullName = fullName;
    this.location = location;
    this.mobile = mobile;
    this.isLoggedIn = isLoggedIn;
  }



  getCurrentUserLoginName() {
    return this.fullName;
  }

  getCurrentUserLoginBoolean() {
    return this.isLoggedIn;
  }

  getPrdoucts() {
    this.productList = this.firebase.list('Products');
    return this.productList.snapshotChanges();
  }
  getSingleProduct(id) {
    return this.firebase.object('/Products/obj' + id).valueChanges();
  }

  saveCartProduct(product) {
    cartProduct.push(product);
  }

  getCartProductArray() {
    return cartProduct;
  }


  getcartproductLenth() {
    return cartProduct.length;
  }

  deleteCartProduct(product) {
    const index = cartProduct.indexOf(product);
    cartProduct.splice(index, 1);
  }

  //Customers ka data


  getCustomers() {
    this.customerList = this.firebase.list('Customers');
    return this.customerList.snapshotChanges();
  }

  insertCustomer(customer) {
    this.customerList = this.firebase.list('Customers');
    this.customerList.push({
      fullName: customer.fullName,
      email: customer.email,
      mobile: customer.mobile,
      location: customer.location,
      confirmPassword: customer.confirmPassword
    });
  }

  //populateForm(customer) {
  //   this.form.setValue(customer);
  // }

  updateCustomer(customer) {
    this.customerList.update(customer.$key,
      {
        fullName: customer.fullName,
        email: customer.email,
        mobile: customer.mobile,
        location: customer.location
      });
  }

  deleteCustomer($key: string) {
    this.customerList.remove($key);
  }

}




