import { Routes } from "@angular/router";
import { MyAccountComponentComponent } from "./my-account-component/my-account-component.component";
import { ContactUsComponentComponent } from "./contact-us-component/contact-us-component.component";
import { CreateAccountComponentComponent } from "./create-account-component/create-account-component.component";
import { HomePageComponentComponent } from "./home-page-component/home-page-component.component";
import { MoreStuffComponentComponent } from "./more-stuff-component/more-stuff-component.component";
import { DemoPageComponentComponent } from "./demo-page-component/demo-page-component.component";
import { DetailPageComponentComponent } from "./detail-page-component/detail-page-component.component";
import { CartCompoentComponent } from "./cart-compoent/cart-compoent.component";

export const AppRoutes: Routes = [
  { path: 'HomePage', component: HomePageComponentComponent },
  { path: 'HomePage/:id/:name', component: HomePageComponentComponent },
  { path: 'DetailPage/:id', component: DetailPageComponentComponent },
  { path: 'MyAccount', component: MyAccountComponentComponent },
  { path: 'DemoPage', component: DemoPageComponentComponent },
  { path: 'ContactUs', component: ContactUsComponentComponent },
  { path: 'CreateAccount', component: CreateAccountComponentComponent },
  { path: 'MoreStuffComingSoon', component: MoreStuffComponentComponent },
  { path: 'CheckOut', component: CartCompoentComponent },
  { path: '', redirectTo: 'HomePage', pathMatch: 'full' }
]
