import { FirebaseService } from './../service/firebase.service';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';
import { PasswordValidators } from './password.validators';
import { Router } from '@angular/router';

@Component({
  selector: 'app-create-account-component',
  templateUrl: './create-account-component.component.html',
  styleUrls: ['./create-account-component.component.css']
})
export class CreateAccountComponentComponent {
  form: FormGroup;
  constructor(private router: Router, private firebaseService: FirebaseService, fb: FormBuilder) {
    this.form = fb.group({
      $key: [null],
      fullName: ['', Validators.required],
      email: ['', [Validators.email, Validators.required]],
      mobile: ['', [Validators.required, Validators.minLength(5)]],
      location: ['', [Validators.required]],
      //  gender: new FormControl('', [Validators.required]),
      newPassword: ['', Validators.required],

      confirmPassword: ['', Validators.required]
    }, {
        validator: PasswordValidators.passwordsShouldMatch,

      });

  }

  submitted: boolean;
  showSuccessMessage: boolean;
  result: boolean;
  // formControls = this.form.controls;

  get fullName() { return this.form.get('fullName'); }
  get email() { return this.form.get('email'); }
  get mobile() { return this.form.get('mobile'); }
  get location() { return this.form.get('location'); }
  get newPassword() { return this.form.get('newPassword'); }
  get confirmPassword() { return this.form.get('confirmPassword'); }

  onSubmit() {
    this.submitted = true;
    if (this.form.valid) {

      if (this.form.get('$key').value == null) {

        this.firebaseService.insertCustomer(this.form.value);
      }
      else {
        this.firebaseService.updateCustomer(this.form.value);
      }

      this.showSuccessMessage = true;
      this.result = true;
      setTimeout(() => this.showSuccessMessage = false, 3000);
      this.submitted = false;
      this.form.reset();
      //this is to be done for proper reset operation
      this.form.setValue({
        $key: null,
        fullName: '',
        email: '',
        mobile: '',
        location: '',
        newPassword: '',
        confirmPassword: ''
      });
    }
    if (this.result) {
      setTimeout(() => this.router.navigate(['/']), 3000);
    }
  }

}
