import { FirebaseService } from './../service/firebase.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-cart-compoent',
  templateUrl: './cart-compoent.component.html',
  styleUrls: ['./cart-compoent.component.css']
})
export class CartCompoentComponent implements OnInit {

  cartProductArray;
  constructor(private firebaseService: FirebaseService) {

  }
  removeProduct(product) {
    this.firebaseService.deleteCartProduct(product);
  }

  ngOnInit() {
    this.cartProductArray = this.firebaseService.getCartProductArray();

  }

}
