import { FirebaseService } from './../service/firebase.service';
import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";




@Component({
  selector: 'app-my-account-component',
  templateUrl: './my-account-component.component.html',
  styleUrls: ['./my-account-component.component.css']
})

export class MyAccountComponentComponent implements OnInit {

  constructor(private router: Router, private firebaseService: FirebaseService) { }
  showSuccessMessage = false;
  showFailureMessage = false;
  result: boolean;
  allCustomerArray = [];

  key;
  confirmPassword: '';
  email: '';
  fullName: '';
  location: '';
  mobile: '';


  ngOnInit() {
    this.firebaseService.getCustomers().subscribe(
      list => {
        this.allCustomerArray = list.map(item => {
          return {
            $key: item.key,
            ...item.payload.val()
          };
        });
      });
    // console.log("Ong");
  }

  signIn(f) {

    //  this.allCustomerArray.forEach(lol => {
    this.allCustomerArray.forEach(value => {
      if (value.fullName === f.username && value.confirmPassword === f.password) {
        console.log('OK');
        this.key = value.$key;
        this.confirmPassword = value.confirmPassword;
        this.email = value.email;
        this.fullName = value.fullName;
        this.location = value.location;
        this.mobile = value.mobile;
        this.showSuccessMessage = true;
        this.result = true;
        setTimeout(() => this.showSuccessMessage = false, 3000);

      }

    });
    //  });

    if (this.result) {
      this.allCustomerArray = [];
      //console.log(this.allCustomerArray, " Destroyed");
      // tslint:disable-next-line: max-line-length
      this.firebaseService.setCurrentUserLoginInfo(this.key, this.confirmPassword, this.email, this.fullName, this.location, this.mobile, true);
      setTimeout(() => this.router.navigate(['/HomePage', this.key, this.fullName]), 3000);
      setTimeout(() => this.showSuccessMessage = false, 3000);
    }
    else {
      this.showFailureMessage = true;
      setTimeout(() => this.showFailureMessage = false, 3000);
    }



  }




}
